import { reactShinyInput } from 'reactR';
import { Button } from 'reactstrap';

function reactActionButton({configuration, value, setValue}) {
  return (
    <Button
      color={configuration.status}
      onClick={() => setValue(value + 1)}>
      {configuration.label}
    </Button>
  );
}

reactShinyInput(
  '.react_action_button',
  'reactstrapTest.action_button',
  reactActionButton
);
